@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

.hero {
  position: relative;
  width: 100%;
  height: 100vh;
}

.hero__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}

.hero__title {
  position: absolute;
  bottom: 25%;
  right: 10%;
  color: white;
  font-size: 4rem;
  padding: 15px;
}

.hero__subtitle {
  position: absolute;
  bottom: 15%;
  right: 10%;
  color: white;
  font-size: 4rem;
  padding: 15px;
}

.navbar {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  height: 70px;
  background-color: black;
  opacity: 0.75;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 30px 0px 30px;
  margin-bottom: -70px;
  z-index: 5;
}

.navbar__logo {
  color: #c3a343;
  font-family: "MonteCarlo", cursive;
  font-size: 2.5rem;
}

/*
  This is the container for all the navbar links ( <ul> )
  */
.navbar__list {
  display: flex;
  list-style: none;
}

/*
  This is a single item ( <li> ) in the list
  */
.navbar__item {
  white-space: nowrap;
}

/*
  The anchor tag ( <a> ) for the navbar links
  */
.navbar__link {
  font-size: 0.85rem;
  text-decoration: none;
  color: #c3a343;
  text-align: center;
  padding: 10px;
  margin: 0px 5px;
  border-style: solid;
  border-width: 0px 0px 1px 0px;
  border-color: transparent;
  transition: 250ms;
}

.navbar__link:hover {
  border-color: #c3a343;
  transition: 250ms;
}

.navbar__menu {
  color: white;
  display: none;
  cursor: pointer;
}

@media screen and (max-width: 800px) {
  .navbar__list {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    top: 70px;
    left: -100%;
    width: 100%;
    height: 300px;
    position: absolute;
    transition: all 0.5s ease;
  }

  .navbar__list--active {
    left: 0;
  }

  .navbar__menu {
    display: block;
  }

  .navbar__item {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0;
    flex: 1 1;
  }

  .navbar__link {
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 0px;
    border-width: 0px;
    background-color: black;
    opacity: 0.75;
  }

  .navbar__link:hover {
    border-width: 0px;
    opacity: 0.9;
  }
}

.slider {
  display: flex;
  align-items: center;
  margin: 20px;

  opacity: 0;
  -webkit-transform: scale(85%);
          transform: scale(85%);
  transition: 2s;
}

.slider--zoom {
  opacity: 1;
  -webkit-transform: scale(100%);
          transform: scale(100%);
  transition: 2s;
}

.slider__image {
  width: 60%;
}

.slider__content {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  text-align: center;
}

.slider__title {
  text-align: center;
}

@media screen and (max-width: 800px) {
  .slider {
    flex-direction: column;
    background-color: rgb(236, 236, 236);
  }

  .slider__image {
    width: 100%;
  }
}

.eventos {
  display: flex;
  align-items: center;
  margin: 20px;

  opacity: 0;
  -webkit-transform: scale(85%);
          transform: scale(85%);
  transition: 2s;
}

.eventos--zoom {
  opacity: 1;
  -webkit-transform: scale(100%);
          transform: scale(100%);
  transition: 2s;
}

.eventos__image {
  width: 60%;
}

.eventos__content {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  text-align: center;
}

.eventos__contentT {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  text-align: left;
}

.eventos__title {
  text-align: left;
}

@media screen and (max-width: 800px) {
  .eventos {
    flex-direction: column;
    background-color: rgb(236, 236, 236);
  }

  .eventos__image {
    width: 100%;
  }
}
body {
  font-family: "Source Sans Pro", sans-serif;
}
#title {
  text-align: center;
  color: white;
  font-size: 30px;
  text-transform: uppercase;
  margin-top: 100px;
}
.hover-table-layout {
  display: flex;
  flex-wrap: wrap;
  max-width: 800px;
  margin: 0 auto;
}
.listing-item {
  display: block;
  width: 100%;
  margin-bottom: 20px;
  float: left;
  background: #fff;
  border-radius: 10px;
  z-index: 0;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.1);
}
.listing-item:hover,
.listing-item.active {
  -webkit-transform: scale(1.03);
  transform: scale(1.03);
  transition: all 0.3s;
  z-index: 2;
}
.listing-item .listing {
  padding: 20px;
  position: relative;
}
.listing-item .listing:before {
  content: "";
  position: absolute;
  top: -15px;
  left: -o-calc(50% - 15px);
  left: calc(50% - 15px);
  border-bottom: 20px solid #fff;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
}
figure.image img {
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
figure.image {
  position: relative;
  margin: 0;
  padding: 0;
}
figure.image figcaption {
  position: absolute;
  top: 0;
  width: 100%;
  text-align: center;
  bottom: 4px;
  background: rgba(0, 0, 0, 0.6);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
figcaption .caption {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
figcaption h1 {
  color: white;
  font-weight: bold;
  font-size: 16px;
  text-transform: uppercase;
}
figcaption p {
  color: white;
  font-size: 12px;
}
.listing h4 {
  font-size: 13px;
  text-align: center;
  padding: 5px 10px;
  font-weight: bold;
}
.listing h4:not(:last-child) {
  border-bottom: 1px solid #ccc;
}
.listing-item:hover figure.image figcaption {
  background: rgba(195, 39, 43, 0.6);
}
@media only screen and (min-width: 540px) {
  .listing-item {
    display: block;
    width: calc(100% / 3);
  }
}
@media only screen and (min-width: 1024px) {
  .hover-table-layout {
    padding: 30px;
  }
}

